import { Food } from "./food-item";

export class Meal {
    static dbName: string  = 'meal';
    Id: number;
    Type: MealType;
    FoodsDiem: Food[] = [];
    FoodsToday: Food[] = [];
    Date: Date = new Date();
}

export class MealType {
    static dbName: string  = 'meal-type'
    Name: string;
    StartTime: string;
    EndTime: string;
}