import { Food, FoodItem } from "@models/diet/food-item";
import { DbContext } from "./indexdb";

export async function GetFoods() {
  return await DbContext.db!.getAll(FoodItem.dbName);
}

export async function SaveFoodsArray(data: Food[]) {
    data.forEach(SaveFood);
}

export async function SaveFood(data: Food) {
  await DbContext.db!.put(FoodItem.dbName, data);
}
