import { DietGoals } from '@models/diet/diet-goals';
import { FoodEntry } from '@models/diet/food-entry';
import { PersonalInfo } from '@models/personal-info';
import { IDBPDatabase, openDB } from 'idb';
import { SaveDietGoals } from '../diet-goals.service';
import { SavePersonalInfo } from '../personal-info.service';
import { FoodItem } from '@models/diet/food-item';
import {
  MealTypeSeed as mealTypeSeed,
  foodSeed,
  goalsSeed,
  profileSeed,
} from './seed-data';
import { SaveFoodsArray } from '../food-items.service';
import { Meal, MealType } from '@models/diet/meal';
import { SaveMealTypeArray } from '@services/meal.service';

const DB_NAME = 'bulk';
const DB_VERSION = 1;

export class DbContext {
  private static _db: IDBPDatabase<any> | undefined;
  private static _needsSeed = false;

  public static get db() {
    return DbContext._db;
  }

  constructor() {
    if (DbContext._db) {
      return;
    }
    openDB(DB_NAME, DB_VERSION, {
      // upgrade(db, oldVersion, newVersion, transaction) {
      upgrade(db, oldVersion) {
        // Switch over the oldVersion, *without breaks*, to allow the database to be incrementally upgraded.
        switch (oldVersion) {
          case 0:
          // Placeholder to execute when database is created (oldVersion is 0)
          case 1:
            // Create a store of objects
            DbContext.createFoodItemStore(db);
            DbContext.createFoodEntryStore(db);
            DbContext.createDietGoalsStore(db);
            DbContext.createPersonalInfoStore(db);
            DbContext.createMealTypes(db);
            DbContext.createMeals(db);
            DbContext._needsSeed = true;
        }
      },
    })
      .then((d) => (DbContext._db = d))
      .then(async (_) => await this._SeedDB());
  }

  private async _SeedDB() {
    if (!DbContext._needsSeed) {
      return;
    }
    return Promise.all([
      SaveDietGoals(goalsSeed),
      SavePersonalInfo(profileSeed),
      SaveFoodsArray(foodSeed),
      SaveMealTypeArray(mealTypeSeed),
    ]);
  }

  private static createFoodItemStore(db: IDBPDatabase<unknown>) {
    if (!db.objectStoreNames.contains(FoodItem.dbName)) {
      db.createObjectStore(FoodItem.dbName, {
        keyPath: 'Name',
      });
    }
  }

  private static createMealTypes(db: IDBPDatabase<unknown>) {
    if (!db.objectStoreNames.contains(MealType.dbName)) {
      const table = db.createObjectStore(MealType.dbName, {
        keyPath: 'Name',
      });
      table.createIndex('StartTime', 'StartTime');
    }
  }

  private static createMeals(db: IDBPDatabase<unknown>) {
    if (!db.objectStoreNames.contains(Meal.dbName)) {
      const table = db.createObjectStore(Meal.dbName, {
        keyPath: 'Id',
        autoIncrement: true,
      });
      table.createIndex('Date', 'Date');
    }
  }

  private static createFoodEntryStore(db: IDBPDatabase<unknown>) {
    if (!db.objectStoreNames.contains(FoodEntry.dbName)) {
      const foodItemsStore = db.createObjectStore(FoodEntry.dbName, {
        keyPath: 'Id',
        autoIncrement: true,
      });
      foodItemsStore.createIndex('Date', 'Date');
    }
  }

  private static createDietGoalsStore(db: IDBPDatabase<unknown>) {
    if (!db.objectStoreNames.contains(DietGoals.dbName)) {
      db.createObjectStore(DietGoals.dbName, {
        autoIncrement: false,
      });
    }
  }

  private static createPersonalInfoStore(db: IDBPDatabase<unknown>) {
    if (!db.objectStoreNames.contains(PersonalInfo.dbName)) {
      db.createObjectStore(PersonalInfo.dbName, {
        autoIncrement: false,
      });
    }
  }
}
