import { Macros } from './macros';
import { FoodCategory, PortionMeasure } from './types';

export class FoodItem {
  static dbName: string = 'food-item';
  Name: string;
  Diem: number = 0;
  Today: number = 0;
  Portion: PortionMeasure = '100g';
  Macros: Macros = new Macros();
  MainCategory: FoodCategory;
}

export class Dish {
  static dbName: string = 'food-item';
  Name: string;
  Diem: number = 0;
  Today: number = 0;
  Ingredients: FoodItem[] = [];
}

export type Food = FoodItem | Dish;
export const isDish = (f: Food) : f is Dish => f  instanceof Dish || 'Ingredients' in f;

export class FoodTable {
  Name: string;
  Today: number;
  Diem: number;
  Calories: number;
  Protein: number;
  Carbs: number;
  Fat: number;
}
