import { DbContext } from "./indexdb";
import { DietGoals } from "@models/diet/diet-goals";

export async function GetDietGoals() {
  return await DbContext.db!.get(DietGoals.dbName, 1);
}

export async function SaveDietGoals(data: DietGoals) {
    await DbContext.db!.put(DietGoals.dbName, data, 1);
}
