import { Meal, MealType } from "@models/diet/meal";
import { DbContext } from "./indexdb";

export async function GetMeal() {
  return await DbContext.db!.getAll(Meal.dbName);
}

export async function SaveMealArray(data: Meal[]) {
    data.forEach(SaveMeal);
}

export async function SaveMeal(data: Meal) {
  await DbContext.db!.put(Meal.dbName, data);
}

export async function GetMealType() {
  return await DbContext.db!.getAllFromIndex(MealType.dbName, 'StartTime');
}

export async function SaveMealTypeArray(data: MealType[]) {
    data.forEach(SaveMealType);
}

export async function SaveMealType(data: MealType) {
  await DbContext.db!.put(MealType.dbName, data);
}
